export function Projects({ data }) {
  return (
    <div className={"flex mt-10 "}>
      <div
        style={{
          writingMode: "vertical-rl",
          textOrientation: "mixed",
          transform: "rotate(180deg)",
        }}
        className={"pt-10 text-xl flex justify-center"}
      >
        <span className={"tracking-widest text-bg"}>{data.title}</span>
      </div>
      <div>
        {data.list.map((e) => (
          <div className={"px-10 pt-5"}>
            <p className={"text-black-100	text-lg font-bold"}>{e.title}</p>
            <p className={"text-black-400	text-basic font-light	"}>
              {e.description}
            </p>
          </div>
        ))}
      </div>
    </div>
  );
}
