import React, { useEffect } from "react";
import { motion, useAnimation } from "framer-motion";

import { Helmet } from "react-helmet";
import Layout from "../../components/layout";

export default function Contact({ data }) {
  const controls = useAnimation();
  useEffect(() => {
    controls.start((i) => ({
      opacity: 1,
      x: 0,
      z: 4,
      y: 0,
      transition: { delay: i * 0.5 },
    }));
  }, [controls]);

  return (
    <Layout>
      <Helmet>
        <meta charSet="utf-8" />
        <title>{data.helmet.title}</title>
      </Helmet>
      <section
        className={"w-full h-full relative model p-5"}
      >
        <div
          className="w-full h-full flex justify-center items-center flex-col relative"
          style={{ zIndex: 10 }}
        >
          <div className="mt-10" />
          <div
            className="splashLogo flex flex-col items-start  p-30"
            style={{ zIndex: 10 }}
          >
            <ul>
              <motion.div
                style={{ x: -100, opacity: 0 }}
                animate={controls}
                custom={2}
                className={" text-6xl text-center"}
              >
                {data.title}
              </motion.div>
              <motion.div
                custom={3}
                animate={controls}
                style={{
                  maxWidth: 800,
                  width: "100%",
                  textAlign: "center",
                  opacity: 0,
                  y: 100,
                }}
                className="text-xl font-thin p-5"
              >
                {data.description}
              </motion.div>
              <motion.div
                custom={4}
                animate={controls}
                style={{ opacity: 0 }}
                className="text-xl font-thin text-center pb-5"
              >
                {data.ending}
              </motion.div>
            </ul>
          </div>

          <motion.div
            custom={5}
            animate={controls}
            style={{ opacity: 0 }}
            className="p-4 border-solid border-2 border-white hover:border-[#243c5a] cursor-pointer"
            whileHover={{ backgroundColor: '#fff', color: '#000' }}
          >
            {data.btn.label}
          </motion.div>
        </div>
      </section>
    </Layout>
  );
}
