export function Experience({ data }) {
  return (
    <div className={"flex "}>
      <div
        style={{
          writingMode: "vertical-rl",
          textOrientation: "mixed",
          transform: "rotate(180deg)",
        }}
        className={"pt-10 text-xl flex justify-center"}
      >
        <span className={"tracking-widest text-bg"}>{data.title}</span>
      </div>
      <div className={"px-10 text-base"}>
        {data.list.map((e) => (
          <div className={"mb-10"}>
            <p>
              <span>{e.range}</span>
              <span className={"	font-normal	"}>{e.company}</span>
            </p>

            <p className={"text-black-300	text-md font-light"}>{e.title}</p>
            <br />
            <p
              className={"text-black-300  font-light"}
              dangerouslySetInnerHTML={{ __html: e.description }}
            />
          </div>
        ))}
      </div>
    </div>
  );
}
