export function Familiar({ data }) {
    return (
      <div className={"flex mt-10"}>
        <div
          style={{
            writingMode: "vertical-rl",
            textOrientation: "mixed",
            transform: "rotate(180deg)",
          }}
          className={"pt-10 text-xl flex justify-center"}
        >
          <p className={"tracking-widest text-bg"}>{data.title}</p>
        </div>
        <div className={"pl-10 flex flex-row flex-wrap text-lg"}>
          {data.list.map((e) => (
            <span className={"px-3 py-3 m-1 border-2 border-gray-400 font-light flex items-center"}>
              {e}
            </span>
          ))}
        </div>
        <div className={"px-10 text-base"}></div>
      </div>
    );
  }
  