import { AnimatePresence, motion, useAnimation } from "framer-motion";
import React, { useEffect, useState } from "react";

import './style.css';
import Helmet from "react-helmet";
import Layout from "../../components/layout";

export default function Project({ data }) {
  const [lightboxImage, setLightboxImage] = useState(null);
  const controls = useAnimation();

  const closeLightbox = () => {
    setLightboxImage(null);
  };

  useEffect(() => {
    controls.start((i) => ({
      opacity: 1,
      x: 0,
      z: 4,
      y: 0,
      transition: { delay: i * 0.2 },
    }));
  }, [controls]);


  return (
    <Layout>
      <Helmet>
        <meta charSet="utf-8" />
        <title>John Mamanao - {data.title}</title>
      </Helmet>
      {lightboxImage && (
        <div className="modal" onClick={closeLightbox}>
          <img
            src={lightboxImage}
            alt=""
            className="modal-image"
            onClick={(e) => e.stopPropagation()}
          />
          <span className="close-button" onClick={closeLightbox}>
            &times;
          </span>
        </div>
      )}
      <div className="max-w-screen-xl m-auto min-h-full h-full">
       <AnimatePresence>
          <motion.div
            layoutId={data.slug}
            className="flex justify-center items-center p-5"
          >
            <img className="w-full object-contain border cursor-pointer" src={data.image} alt="" onClick={() => {setLightboxImage(data.image);}}/>
          </motion.div>
        </AnimatePresence>
        <div className="w-full flex flex-col justify-center p-5">
          <motion.div
            custom={1}
            animate={controls}
            className="text-4xl font-extrabold"
            style={{ opacity: 0, x: -10 }}
          >
            {data.title}
          </motion.div>
          <br />
          <motion.div
            custom={1}
            animate={controls}
            style={{ opacity: 0, x: -10 }}
            className="text-xl font-thin"
          >
            {data.description}
          </motion.div>
          <br />
          <motion.div
            custom={2}
            animate={controls}
            style={{ opacity: 0, x: -10 }}
            className="text-xl font-thin"
          >
            <b>Technology</b> : {data.technology.map((e) => ` ${e},`)}
          </motion.div>
          <motion.div
            custom={3}
            animate={controls}
            style={{ opacity: 0, y: -10 }}
            className="flex gap-5 mt-10"
          >
            {data.link.map((e) => (
              <a
                href={e.url}
                className="text-xl p-2 border-solid border-2 border-white-500 hover:border-[#243c5a] hover:bg-white hover:text-black cursor-pointer"
              >
                {e.text}
              </a>
            ))}
          </motion.div>
        </div>
      </div>
    </Layout>
  );
};
